@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body{margin:0;padding:0;font-family:"Open Sans",sans-serif;font-size:18px;line-height:1.5;color:#000;letter-spacing:0;text-align:left;background-color:#f5f5f5}img{max-width:100%}.section-pt{padding-top:60px}.section-pb{padding-bottom:60px}.section-title h2{font-size:48px}.navbar.bg-trans{padding-top:22px;padding-bottom:22px}.navbar.bg-trans .navbar-brand{max-width:130px}.navbar.bg-trans .nav-item{padding:0 10px}.navbar.bg-trans .nav-link{text-transform:uppercase;color:#fff;position:relative}.navbar.bg-trans .nav-link:hover{color:#fff}.navbar.bg-trans .nav-link::after{content:"";position:absolute;width:0;bottom:0;left:0;height:2px;background:#fff;right:0;margin:auto;-webkit-transition:0.5s ease-in-out;transition:0.5s ease-in-out}.navbar.bg-trans .nav-link:hover::after{width:100%}.navbar.bg-trans .nav-link.active::after{width:100%;color:#fff}.home-banner-top{background:url(/demo-project/static/media/banner-bg.2f8dd407.jpg) no-repeat center center;background-size:cover;background-attachment:fixed;height:100vh;position:relative;text-align:center}.home-banner-top .banner-content{display:flex;flex-direction:column;height:100vh;max-height:100%;justify-content:center}.home-banner-top .banner-content .col-sm-12{flex:initial}.home-banner-top h1{font-size:60px;color:#fff;font-weight:600}.home-banner-top .banner-subtitle p{color:#fff;font-size:40px}.aboutSection .shadow-area{box-shadow:0 0 5px rgba(0,0,0,0.3);padding:60px;background-color:#fff}

